@import 'variables/colors.scss';
@import 'variables/general.scss' ;

/* section about */
.about{
    display: flex;
    flex-direction: column;
    padding: 1em 3em;
    background-color: $lightcolor;
}
section.about > h2 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-self: center;
    padding-bottom: 6rem 0 5rem 0 ;
    color:$darkcolor ;
    
}
div.about.container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    height: 100%;
    align-items: center;
    justify-content: space-around;
}
div.pic > img {
    display: flex;
    flex-direction: column;
    border-radius: 60%;
    -webkit-border-radius: 60%;
    -moz-border-radius: 60%;
    -ms-border-radius: 60%;
    -o-border-radius: 60%;
    width:20em;
    height: 20em;
    

}

div.pic{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex: 1 1;
    align-items: center;
    
}

div.info{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex: 1 1 ;
 
}

.button{
    width: 30%;
    margin: 1em 0;;
    background-color: $darkcolor;
    color: $lightcolor;
    border: none;
    border-radius: 15%;
    -webkit-border-radius: 15%;
    -moz-border-radius: 15%;
    -ms-border-radius: 15%;
    -o-border-radius: 15%;
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    padding: 0.4em;
    cursor: pointer;
    &:focus {
        outline: none;
    } 

}